:root {
    --p: #33b3f3;
  }

.parallax {
    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.bottom-margin{
    margin-bottom:25px;
}


.heading-container{
    padding:25px;
}

.section{
    padding-top:50px;
    padding-bottom:50px;
}

.section-heading{
    font-family: "Source Sans Pro", Helvetica, sans-serif;
    font-size: 3rem;
    font-weight: 700;
    letter-spacing: -0.05em;
    text-transform: none;
    text-transform: uppercase;
}

.section-subheading{
    font-family: "Source Sans Pro", Helvetica, sans-serif;
    font-size: 2rem;
    font-weight: 500;
    letter-spacing: +0.05em;
    text-transform: none;
    text-transform: uppercase;
}   



.homeImg{
    max-width: 100%;
    height: auto;
    width: auto\9; /* ie8 */
}

.featuresTitle{
    font-size: 2.3rem;
}

.slope{
    background-color: transparent;
    clip-path: polygon(20% 0, 100% 0, 100% 100%, 0% 100%);
    padding-left:29%;
}

.ig{
    color: var(--p);
}

.servicesText{
    font-size: 1.33rem;
}

.footerText{
    font-size: 1.20rem;
}

.planText{
    font-size: 1.5rem;
}

.homeText{
    margin-left:23%;
}


.emailAd:hover{
    color: var(--p);
}
.HomeContent{
    background-color: transparent;
    padding-top:5%;
    padding-bottom: 5%;
}

.Home{
    background-image: url('./assets/images/backgroundSmall.jpeg');
    height:80vh;
    background-repeat: no-repeat;
    background-size: cover;
    width:100%;    
}

.plan{
    border: 1px solid gray;
    text-align: center;
    padding: 2%;
}
.plan{
    border: 1px solid orange;
    text-align: center;
    padding: 2%;
}
.plan{
    border: 1px solid var(--p);
    text-align: center;
    padding: 2%;
}
.plan1{
    /*border-bottom: 1px solid var(--p);*/
    text-align: center;
    padding: 2%;
}
.plan2{
    /*border-bottom: 1px solid var(--p);*/
    border-left:1px solid var(--p);
    text-align: center;
    padding: 2%;
}
.plan3{
    /*border-bottom: 1px solid var(--p);*/
    border-left:1px solid var(--p);
    border-right:1px solid var(--p);
    text-align: center;
    padding: 2%;
}

.plan4{
    border-top: 1px solid var(--p);
    text-align: center;
    padding: 2%;
}
.plan5{
    border-top: 1px solid var(--p);
    border-left:1px solid var(--p);
    text-align: center;
    padding: 2%;
}
.plan6{
    border-top: 1px solid var(--p);
    border-left:1px solid var(--p);
    border-right:1px solid var(--p);
    text-align: center;
    padding: 2%;
}   


.form-control{
    border:none;
    border-bottom: 1px solid var(--p);
    margin-bottom: 2%;
}
textarea.form-control{
    border:none;
    border: 1px solid var(--p);
}

.formButton{
    width:50vw;
    border-radius: 16px;
    font-weight: 500;
}

.formButton1{
    width:40%;
    border-radius: 16px;
    font-weight: 500;
}

.nav-footer, .footerContact{
    padding-top:3%
}
/* small*/
@media screen and (max-width: 767px) {
    .Home{
        height: 100vh;
    }

    .parallax {
        /* Remove the parallax scrolling effect */
        background-attachment: scroll;

    }

    .logo{
        display:none;
    }

    .section-heading{
        font-family: "Source Sans Pro", Helvetica, sans-serif;
        font-size: 2rem;
        font-weight: 700;
        letter-spacing: -0.05em;
        text-transform: none;
        text-transform: uppercase;
    }
    
    .section-subheading{
        font-family: "Source Sans Pro", Helvetica, sans-serif;
        font-size: 1rem;
        font-weight: 500;
        letter-spacing: +0.05em;
        text-transform: none;
        text-transform: uppercase;
    }   

    
    
   
        .plan1{border: none;}
        .plan2{border: none;}
        .plan3{border: none;}
        .plan4{border: none;}
        .plan5{border: none;}
        .plan6{border: none;}
    
    .HomeContent{
        height:100vh;
    }
    .Home{
        background-position: center;
    }
    .nav-footer{
        display: none;
    }
   .footerSocial{
       text-align:left !important;
   }



    .Services{
        padding:5%;
        
    
    }
    .servicesText{
        font-size: 1.1rem;
    }
    .planText{
        font-size: 1.2rem;
    }
    .display-3{
        font-size:3.5rem;
    }
    .display-4{
        font-size:3rem;
    }
}
/*Medium*/
@media screen and (min-width: 768px) {
    .Home{
        background-image: url('./assets/images/HomePageImageCut.jpeg');
        height:80vh;
          background-repeat: no-repeat;
          background-size: cover;
          width:auto;
          background-position: left;
    }
    
    .mobileOnly{display: none;}

    .aboutText{
        font-size: 1.3rem;
    }
    
}

.container-padding{
    padding-top:50px;
    padding-bottom:50px;
}

.aboutText{
    font-size: 1.4rem;
}

.bigBtn{
    font-size:1.5rem;
}
/*Large Screen*/
@media screen and (min-width: 992px) {
    .Home{
        background-image:
        url('./assets/images/background_large_colour.jpeg');
        height:80vh;
        background-repeat: no-repeat;
        background-size: cover;
        width:auto;
        background-position: top;
    }
    
}



.hpText{
    font-size:  1.8rem;
}

.btn-outline-dark{background-color: transparent;}

.zoom {
  
    transition: transform .2s; /* Animation */
   
  }
  
  .zoom:hover {
    transform: scale(1.1); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
    cursor: pointer;
  }

  a{
      color:inherit;    
  }



.featureBox{
    background-color: white;
    padding:2%;
    border: 1.75px solid var(--p);
    border-radius: 2%;
    margin-bottom:5%;
    height:90%
      
}
.featureBox {
    /* Add shadows to create the "card" effect */
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
  }
  
  /* On mouse-over, add a deeper shadow */
  .featureBox:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }
.Services{
    min-height: 80vh;
    text-align: center;
}

.Contact{
    min-height: 70vh;
    padding: 5%;
    text-align: center;
}


b{
    color: var(--p);
}

.alt-bold{
    color:white;
    font-weight:900;
}

.btn-outline-main {
    color: var(--p);
    border-color: var(--p);
    background-color: transparent;
}

.btn-outline-main:hover {
    color: #e3e3e3;
    border-color: var(--p);
    background-color: var(--p) !important;
}

.btn-outline-main:active {
    color: var(--p);
    border-color: var(--p);
    background-color: transparent;
}

.Footer{
    background-color: #343a40 !important;
    
    padding:1%;
}


